:root {
  --font-body: Arial, sans-serif;
  --font-inter: "Inter", sans-serif;
  --font-lexend: "Lexend Deca", sans-serif;
}

:root {
  --black: hsl(0, 0%, 0%);
  --verydarkblue: hsl(233, 47%, 7%);
  --darksaturatedblue: hsl(244, 38%, 16%);
  --softviolet: hsl(277, 64%, 61%);
  --white: hsl(0, 0%, 100%);
  --whitemain: hsla(0, 0%, 100%, 0.75);
  --whitestat: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-inter);
  font-size: 15px;
  background: var(--verydarkblue);
  color: var(--white);
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  border-radius: 0.3125rem;
  padding: 0 1.5rem;
  max-width: 72.375rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.card {
  width: 100%;
  height: 27.875rem;
  background: var(--darksaturatedblue);
  margin-top: 5.5rem;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  overflow: hidden;
}
@media (min-width: 56.75em) {
  .card {
    flex-direction: row-reverse;
    margin-top: 11.0625rem;
  }
}
.card__image {
  background-color: var(--softviolet);
  background-blend-mode: multiply;
  background-image: url("../images/image-header-mobile.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
  width: 100%;
  height: 15rem;
}
@media (min-width: 56.75em) {
  .card__image {
    background-image: url("../images/image-header-desktop.jpg");
    width: 33.75rem;
    height: auto;
  }
}
@media (min-width: 56.75em) {
  .card__details {
    width: 35.625rem;
    height: 100%;
  }
}
.card__title {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 1.75rem;
  width: 100%;
  padding: 0 1.875rem;
}
@media (min-width: 56.75em) {
  .card__title {
    font-size: 2.25rem;
    max-width: 24.0625rem;
    margin-top: 4.625rem;
    margin-left: 4.5rem;
    padding: 0;
    text-align: left;
  }
}
.card__title font {
  color: var(--softviolet);
}